<template lang="pug">
  
  .ol-bank-header
    .ol-bank-header__user-info
      .ol-bank-header__user-icon {{player.name[0]}}
      p.ol-bank-header__user {{player.name}}
    
    .bank-balance
      h1.bank-balance__title Баланс счета:
      p.bank-balance__balance $ {{getFormattedBalance}}

</template>

<script>
  
  import {mapState} from 'vuex'

  import gsap from 'gsap'

  export default {
    data() {
      return {tweenedNumber: 0}
    },
    computed: {
      ...mapState(['player', 'bank']),
      getFormattedBalance() {
        return this.bank.balance.toLocaleString('en')
      }
    },
  }

</script>
