<template lang="pug">
  
  .bank-catalog__list
    .swiper-container
      .swiper-wrapper
        .swiper-slide(v-for='(item, index) in bank.catalogList' v-if='bank.estateBalance[item.id].has')
          .bank-catalog__item(@click='goToPath(item.pathName)')
            .bank-catalog__icon
              svg
                use(:xlink:href='bank.catalogIcons[item.title]')
            .bank-catalog__title {{item.title}}

</template>

<script>

  import {mapState} from 'vuex'

  import Swiper from 'swiper'
  import 'swiper/swiper-bundle.css'

  export default {
    computed: {
      ...mapState(['bank'])
    },
    methods: {
      goToPath(pathName) {
        this.$router.push({name: pathName})
      }
    },
    mounted() {
      let swiper = new Swiper('.swiper-container', {
        freeMode: true,
        slidesPerView: 'auto',
        slidesOffsetAfter: 25,
        spaceBetween: 7,
      })

      this.$nextTick(() => swiper.update())

    }
  }

</script>
