<template lang="pug">
  
  .bank-history__list
    .bank-history__item(v-for='(item, index) in getBankHistory.slice(0,showed)')
      .bank-history__info
        svg
          use(:xlink:href='bank.catalogIcons[item.title]')

        p.bank-history__type {{item.title}}
      
      .bank-history__money(
        :class='item.type === "deposit" ? "bank-history__money_deposit" : "bank-history__money_withdraw"'
      ) {{item.type === "deposit" ? "+" : "-"}}${{item.money}}
    
    button(v-if='showed < bank.history.length' @click='showMore').bank-history__load Загрузить еще

</template>

<script>

  import {mapState} from 'vuex'


  export default {
    data() {
      return {showed: 5}
    },
    methods: {
      showMore() {
        let length = this.bank.history.length;
        
        if (this.showed + 5 < length)
          this.showed += 5
        else 
          this.showed = length

      }
    },
    computed: {
      ...mapState(['bank']),
      getBankHistory() {
        let history = [...this.bank.history];

        return history.sort((a,b) => b.time - a.time)

      }
    },
    created() {
      this.$store.commit('CHECK_HISTORY_PAYMENTS')
    },
    updated() {
      this.$store.commit('CHECK_HISTORY_PAYMENTS')
    }
  }

</script>
